@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;600&display=swap);
:root {
  --dark-blue-dark: hsl(209, 23%, 22%);
  --very-dark-blue-dark: hsl(207, 26%, 17%);
  --very-dark-blue-light: hsl(200, 15%, 8%);
  --very-light-gray-light:  hsl(0, 0%, 98%);
  --dark-gray-light: hsl(0, 0%, 52%);
  --white-dark: hsl(0, 0%, 100%);
  --box-shadow: rgba(65, 65, 65, 0.12) 0px 1px 3px, rgba(58, 58, 58, 0.24) 0px 1px 2px;;
}
html {
  box-sizing: border-box;
}
* {
  margin: 0;
  padding: 0;
}
body {
  font-family: 'Nunito', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

a {
  text-decoration: none;
  color: #000;
}
img {
  width: 100%;
}
.dark {
  background-color: var(--very-dark-blue-dark);
}
.light {
  background-color: var(--very-light-gray-light);
}
.App {
  height: 100vh !important;
}
.container {
  padding: 2rem 3rem;
  overflow-x: hidden;
}

@media only screen and (max-width: 600px) {
  .container {
    padding: 1rem;

  }
}
.detailContainer {
  display: grid;
  height: calc(100vh - 4rem);
  grid-template-columns: 1fr 1fr;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 10px;
  gap: 10px;
  margin: 1rem 0;
}
@media only screen and (max-width: 900px) {
  .detailContainer {
    grid-template-columns: 1fr .8fr;
  }
}
@media only screen and (max-width: 768px) {
  .detailContainer {
    grid-template-columns: 1fr;
  }
}
@media only screen and (max-width: 600px) {
  .detailContainer {
    display: block;
    grid-template-columns: 1fr;
  }
}

.countryDetails {
  display: grid;
}

.backBtn {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 90px;
  height: 35px;
  font-size: .9rem;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  box-shadow: var(--box-shadow);
}

.countryName {
  font-size: 1.7rem;
  font-weight: bold;
  margin: 1rem 0;
}

.extraInfo {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

@media only screen and (max-width: 600px) {
  .extraInfo {
    -webkit-flex-direction: column;
            flex-direction: column;

  }
}

.extraInfo p {
  font-weight: bold;
  margin-bottom: .5rem;
  font-size: 1.1rem
}

.borderCountries {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-justify-content: stretch;
          justify-content: stretch;
  margin-top: 2rem;
}

.borderCountries p {
  font-weight: bold;
}

.borderList {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-row-gap: 10px;
}

@media only screen and (max-width: 600px) {
  .borderList {
    grid-template-columns: repeat(4, 1fr);
    -webkit-justify-content: flex-start;
            justify-content: flex-start;

  }
}

.borders {
  /* background-color: #ccc; */
  padding: .2rem 1rem;
  margin: 0 .2rem;
  font-size: .8rem;
  border-radius: 3px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  box-shadow: var(--box-shadow);
}

.bigFlagContainer {
  padding-right: 6rem;
}
@media only screen and (max-width: 900px) {
  .bigFlagContainer {
    padding-right: 2rem;
  }
}
@media only screen and (max-width: 600px) {
  .bigFlagContainer {
    padding: 0;
  }
}

.bigFlag {
  box-shadow: var(--box-shadow);
}

.smallText {
  font-size: 1rem;
}

.ddownContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  max-width: 210px;
  padding: .8rem;
  border-radius: 4px;
  box-shadow: var(--box-shadow);
  position: inherit;
  cursor: pointer;
}
.dText {
  font-size: .8rem;
  padding-right: .9rem;
}
.dIcon {
  color: #000;
  cursor: pointer;
}
.dropMenu {
  position: absolute;
  top: 3rem;
  width: 115px;
  padding: 1rem;
  z-index: 1111;
  box-shadow: var(--box-shadow);
  border-radius: 4px;
}
.dropMenu p {
  font-size: .9rem;
  font-weight: 500;
  margin-bottom: .3rem;
  cursor: pointer;
}
.searchContainer {
  display: grid;
  grid-template-columns: .4fr .5fr;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
  padding: 2rem 3rem 0 3rem;
}
@media only screen and (max-width: 600px) {
  .searchContainer {
    grid-template-columns: 1fr;
    padding: 2rem 1rem 0 1rem;
  }
}
.searchBox {
display: -webkit-flex;
display: flex;
padding:.8rem 1rem;
border-radius: 5px;
box-shadow: var(--box-shadow);

}

.searchInput {
  border: none;
  font-size: .9rem;
  width: 100%;
  outline: none;
  height: 100%;
}
.regionContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  justify-self: flex-end;
  position: relative;
}
@media only screen and (max-width: 600px) {
  .regionContainer {
    justify-self: flex-start;
   
  }
}

.countryCard {
  width: 14rem;
  min-height: 300px;
  border-radius: 5px;
  box-shadow: var(--box-shadow);
}
@media only screen and (max-width: 768px) {
  .countryCard {
    width: 100%;
  }
}
@media only screen and (max-width: 600px) {
  .countryCard {
    width: 100%;
  }
}

.countryInfo {
  padding: 1rem ;
}
.name {
  font-size: 1.2rem;
  font-weight: bold;
  margin-bottom: .5rem;
}
.population, .region, .capital {
  font-size: .9rem;
  margin-bottom: .2rem;
}

.countryContainer {
  /* height: calc(100vh - 3.5rem); */
  display: grid;
}
.countryLists {
  
  display: grid;
  grid-template-columns: repeat(4, auto);
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 10px;
  gap: 10px;
  grid-row-gap:  80px;
  row-gap:  80px;
  margin-top: 2rem;
}
@media only screen and (max-width: 1024px) {
  .countryLists {
    grid-template-columns: repeat(3, auto);
  }
}
@media only screen and (max-width: 768px) {
  .countryLists {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
    gap: 20px;
  }
}
@media only screen and (max-width: 600px) {
  .countryLists {
    grid-template-columns: 1fr;

  }
}
.spinner {
  display: grid;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100vh;
}
.lds-roller {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-roller div {
  -webkit-animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  -webkit-transform-origin: 40px 40px;
          transform-origin: 40px 40px;
}
.lds-roller div:after {
  content: " ";
  display: block;
  position: absolute;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: #ccc;
  margin: -4px 0 0 -4px;
}
.lds-roller div:nth-child(1) {
  -webkit-animation-delay: -0.036s;
          animation-delay: -0.036s;
}
.lds-roller div:nth-child(1):after {
  top: 63px;
  left: 63px;
}
.lds-roller div:nth-child(2) {
  -webkit-animation-delay: -0.072s;
          animation-delay: -0.072s;
}
.lds-roller div:nth-child(2):after {
  top: 68px;
  left: 56px;
}
.lds-roller div:nth-child(3) {
  -webkit-animation-delay: -0.108s;
          animation-delay: -0.108s;
}
.lds-roller div:nth-child(3):after {
  top: 71px;
  left: 48px;
}
.lds-roller div:nth-child(4) {
  -webkit-animation-delay: -0.144s;
          animation-delay: -0.144s;
}
.lds-roller div:nth-child(4):after {
  top: 72px;
  left: 40px;
}
.lds-roller div:nth-child(5) {
  -webkit-animation-delay: -0.18s;
          animation-delay: -0.18s;
}
.lds-roller div:nth-child(5):after {
  top: 71px;
  left: 32px;
}
.lds-roller div:nth-child(6) {
  -webkit-animation-delay: -0.216s;
          animation-delay: -0.216s;
}
.lds-roller div:nth-child(6):after {
  top: 68px;
  left: 24px;
}
.lds-roller div:nth-child(7) {
  -webkit-animation-delay: -0.252s;
          animation-delay: -0.252s;
}
.lds-roller div:nth-child(7):after {
  top: 63px;
  left: 17px;
}
.lds-roller div:nth-child(8) {
  -webkit-animation-delay: -0.288s;
          animation-delay: -0.288s;
}
.lds-roller div:nth-child(8):after {
  top: 56px;
  left: 12px;
}
@-webkit-keyframes lds-roller {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes lds-roller {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
nav {
  color: var(--very-dark-blue-light);
  height: 3.5rem;
  display: -webkit-flex;
  display: flex;
  padding: 0 3rem;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  box-shadow: var(--box-shadow);
  
}
@media only screen and (max-width: 600px) {
  nav {
   padding: 0 1rem;
   
  }
}
@media only screen and (max-width: 600px) {
  .appTitle {
  font-size: 1.2rem;
   
  }
}


